.CustomSwitch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.CustomSwitch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.CustomSwitch .slider:before {
  position: absolute;
  content: "";
  height: 12px;
  width: 12px;
  left: 1px;
  bottom: 1.5px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.CustomSwitch input:checked + .slider {
  background-color: #0a9c00;
}

.CustomSwitch input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

.CustomSwitch input:checked + .slider:before {
  -webkit-transform: translateX(13px);
  -ms-transform: translateX(13px);
  transform: translateX(13px);
}

.CustomSwitch .slider.round {
  border-radius: 34px;
}

.CustomSwitch .slider.round:before {
  border-radius: 50%;
}

